import React, { useEffect, useState, createContext, useContext } from 'react';
import { WindowWrapper } from '../window/wrapper';

const PanelContext = createContext();

export const usePanelContext = () => {
  const context = useContext(PanelContext);
  if (!context) {
    throw new Error('usePanelContext must be used within a PanelProvider');
  }
  return context;
};

export const PanelProvider = ({ children }) => {
  const [pyodide, setPyodide] = useState(null);
  const [error, setError] = useState(null);
  const [loadingStatus, setLoadingStatus] = useState('Loading...');

  const [dots, setDots] = useState('');

  useEffect(() => {
    const interval = setInterval(() => {
      setDots(d => d.length >= 3 ? '' : d + '.');
    }, 500);
    return () => clearInterval(interval);
  }, []);

  useEffect(() => {
    const loadPyodide = async () => {
      try {
        // Check for WebAssembly support
        if (typeof WebAssembly === 'undefined') {
          const errMsg = 'WebAssembly is not supported in this browser.\n' +
                        'Please use a modern browser that supports WebAssembly.';
          setError(errMsg);
          throw new Error(errMsg);
        }

        // Check for SharedArrayBuffer support (needed by Pyodide)
        if (typeof SharedArrayBuffer === 'undefined') {
          const errMsg = 'SharedArrayBuffer is not supported in this browser.\n' +
                        'This could be due to:\n' +
                        '1. Browser security settings\n' +
                        '2. Missing COOP/COEP headers\n' +
                        '3. Using an unsupported browser\n\n' +
                        'Try using a different browser or enabling cross-origin isolation.';
          setError(errMsg);
          throw new Error(errMsg);
        }

        if (!window.loadPyodide) {
          const errMsg = 'Pyodide script not found. This could be due to:\n' +
                        '1. Network connectivity issues\n' + 
                        '2. CDN being temporarily unavailable\n' +
                        '3. Content blocker preventing script load\n\n' +
                        'Try refreshing the page or checking your internet connection.\n\n' +
                        'Console logs may provide additional details.';
          setError(errMsg);
          throw new Error(errMsg);
        }

        // Log browser capabilities
        console.log('Browser capabilities:', {
          webAssembly: typeof WebAssembly !== 'undefined',
          sharedArrayBuffer: typeof SharedArrayBuffer !== 'undefined',
          crossOriginIsolated: window.crossOriginIsolated,
          pyodideAvailable: typeof window.loadPyodide !== 'undefined'
        });

        // Load Pyodide with detailed status
        setLoadingStatus('Checking environment...');
        console.log('Starting Pyodide initialization...');
        
        // Add timeout to detect stalled loading
        const timeout = setTimeout(() => {
          setError('Pyodide initialization timed out after 30 seconds. This may indicate:\n' +
                   '1. Slow internet connection\n' +
                   '2. Browser memory constraints\n' +
                   '3. CPU/resource limitations\n\n' +
                   'Try refreshing the page or closing other browser tabs.');
        }, 30000);
        // Configure Pyodide with explicit indexURL and logging
        const pyodideInstance = await window.loadPyodide({
          indexURL: "https://cdn.jsdelivr.net/pyodide/v0.27.2/full/",
          stdout: (msg) => {
            console.log("Pyodide stdout:", msg);
            setLoadingStatus(prev => prev + "\n" + msg);
          },
          stderr: (msg) => {
            console.error("Pyodide stderr:", msg);
            setError(prev => prev ? `${prev}\n${msg}` : msg);
          }
        });

        // Test basic Python functionality before loading packages
        try {
          console.log("Testing Python execution...");
          setLoadingStatus('Testing Python execution...');
          const version = pyodideInstance.runPython('import sys; sys.version');
          console.log("Python version:", version);
          setLoadingStatus(prev => `${prev}\nPython version: ${version}`);
        } catch (e) {
          console.error("Failed basic Python test:", e);
          setError(prev => `${prev}\nFailed basic Python test: ${e.message}`);
          throw e;
        }
        
        // Only proceed with package loading if basic Python works
        setLoadingStatus('Loading packages...');
        try {
          console.log("Loading numpy package...");
          setLoadingStatus('Loading numpy package...');
          
          // Load numpy with more detailed progress
          await pyodideInstance.loadPackage('numpy', {
            messageCallback: (msg) => {
              console.log("Loading numpy:", msg);
              setLoadingStatus(prev => `${prev}\nNumpy: ${msg}`);
            },
            checkIntegrity: false
          });

          // Test numpy after loading
          try {
            console.log("Testing numpy...");
            setLoadingStatus(prev => `${prev}\nTesting numpy...`);
            pyodideInstance.runPython('import numpy as np; np.array([1,2,3])');
            console.log("Numpy test successful");
            setLoadingStatus(prev => `${prev}\nNumpy test successful`);
          } catch (e) {
            console.error("Numpy test failed:", e);
            setError(prev => `${prev}\nNumpy test failed: ${e.message}`);
            throw e;
          }

          // Run garbage collection to free memory
          if (pyodideInstance.runPython) {
            try {
              pyodideInstance.runPython('import gc; gc.collect()');
            } catch (e) {
              console.warn('Failed to run garbage collection:', e);
            }
          }
          
          clearTimeout(timeout);
          setPyodide(pyodideInstance);
          setLoadingStatus('Setup complete');
          setError(null);
        } catch (packageErr) {
          console.error('Package loading error:', packageErr);
          setError(`Failed to load packages: ${packageErr.message}`);
          setLoadingStatus('Failed to load packages');
          throw packageErr;
        }
      } catch (err) {
        console.error('Pyodide error:', err);
        setError(err.message);
        setLoadingStatus('Failed');
      }
    };

    loadPyodide();
  }, []);

  const LoadingIndicator = () => (
      <div className="fixed bottom-4 right-4 bg-white dark:bg-gray-800 p-4 rounded-lg shadow-lg max-w-md">
        <div className="flex items-center space-x-2">
          <div className="w-4 h-4 border-2 border-blue-500 border-t-transparent rounded-full animate-spin"></div>
          <div className="text-sm font-medium whitespace-pre-wrap">
            {loadingStatus}{dots}
          </div>
        </div>
        {error && (
          <div className="mt-2 text-sm text-red-500 whitespace-pre-wrap overflow-auto max-h-32">
            {error}
          </div>
        )}
      </div>
  );

  return (
    <PanelContext.Provider value={{ pyodide, error, loadingStatus }}>
      {loadingStatus !== 'Setup complete' && <LoadingIndicator />}
      {children}
    </PanelContext.Provider>
  );
};

export const PanelWindow = ({ 
  id, 
  initialPosition = { x: 100, y: 100 },
  title = "Panel Visualization",
  pythonCode,
  className = ""
}) => {
  const { pyodide, error: contextError, loadingStatus } = usePanelContext();
  const [error, setError] = useState(null);
  const targetId = `panel-${id}`;

  useEffect(() => {
    const executeVisualization = async () => {
      if (!pyodide) return;

      try {
        const code = `
import panel as pn
import numpy as np

# Get the target div
target = js.document.getElementById("${targetId}")
if target:
    target.innerHTML = ""

# Execute visualization code
${pythonCode}
`;
        await pyodide.runPythonAsync(code);
        setError(null);
      } catch (err) {
        setError(err.message || 'Unknown error occurred');
      }
    };

    executeVisualization();
  }, [pyodide, pythonCode, targetId]);

  const renderContent = () => {
    if (contextError || error) {
      return (
        <div className="p-4 bg-red-100 dark:bg-red-900 text-red-700 dark:text-red-100 rounded">
          <div className="font-bold">Error:</div>
          <div className="text-sm mt-1">{contextError || error}</div>
        </div>
      );
    }

    if (loadingStatus !== 'Setup complete') {
      return (
        <div className="p-4">
          <div className="animate-pulse">
            <div className="h-4 bg-gray-200 dark:bg-gray-700 rounded w-3/4 mb-2"></div>
            <div className="h-4 bg-gray-200 dark:bg-gray-700 rounded w-1/2"></div>
          </div>
          <div className="mt-2 text-sm text-gray-600 dark:text-gray-400">
            {loadingStatus}
          </div>
        </div>
      );
    }

    return (
      <div 
        id={targetId}
        className="min-h-[200px] min-w-[300px]"
      />
    );
  };

  return (
    <WindowWrapper
      id={id}
      initialPosition={initialPosition}
      className={`bg-white dark:bg-gray-800 rounded-lg shadow-lg overflow-hidden ${className}`}
    >
      <div data-window-header className="p-2 cursor-move bg-gray-100 dark:bg-gray-700 flex justify-between items-center">
        <span>{title}</span>
      </div>
      <div className="p-4">
        {renderContent()}
      </div>
    </WindowWrapper>
  );
};
